<template>
  <section class="modifyPage">
    <div class="option-menu-card">
      <el-form :inline="true" ref="formData" :model="formData" size="small">

        <div class="form-inline-warp">
            <el-form-item label="标题">
              <el-input v-model="formData.title" placeholder="标题"></el-input>
            </el-form-item>
            <el-form-item label="类型">
              <el-select v-model="formData.type" placeholder="请选择">
                <el-option v-for="(item,index) in blogsTypeList" :key="index" :label="item.label" :value="item.code"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="标签">
              <el-select v-model="formData.subtag" placeholder="请选择" multiple collapse-tags :multiple-limit="3">
                <el-option v-for="(item,index) in subtagList" :key="index" :label="item.label" :value="item.code"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item>
              <el-checkbox v-model="formData.original" label="原创" border></el-checkbox>
            </el-form-item>
            <el-form-item label="原文链接" class="snowfall-width" :style="{width:!formData.original?'330px':''}">
              <el-input v-model="formData.snowfall" placeholder="原文链接" style="width: 250px;"></el-input>
            </el-form-item>
        </div>
        <el-form-item style="float: right">
          <el-button type="primary" @click="generatingCode">生成至剪切板</el-button>
        </el-form-item>
      </el-form>
    </div>
    <mavon-editor id="modifyPage-mavon-editor" v-model="formData.content"></mavon-editor>
  </section>
</template>

<script>
    export default {
      name: "index",
      data(){
          return{
            blogsTypeList:[
              {label:'记录',code:'record'},
              {label:'技术',code:'technical'},
            ],
            subtagList:[
              {label:'记录',code:'re1cord'},
              {label:'技术',code:'techn2ical'},
              {label:'记录',code:'re3cord'},
              {label:'技术',code:'techn4ical'},
              {label:'记录',code:'re5cord'},
              {label:'技术',code:'techn6ical'},
              {label:'记录',code:'re7cord'},
              {label:'技术',code:'techn8ical'},
              {label:'记录',code:'re9cord'},
              {label:'技术',code:'techn10ical'},
              {label:'记录',code:'re11cord'},
              {label:'技术',code:'techn12ical'},
            ],
            formData: {
              date: 0,
              title: '',
              content: '',
              type: 'record',    //类型 /记录 技术
              subtag: [],        //标签
              choiceness: false, //是否精选
              original: true,    //是否原创
              snowfall: '',  //原文链接
            },
          }
      },
      watch:{
      },
      mounted() {

      },
      methods:{
        generatingCode(){
          this.formData.date = new Date().getTime();
          console.log(JSON.stringify(this.formData));
          let inputDom = this.createInput();
          console.log(inputDom)
          inputDom.value = JSON.stringify(this.formData);
          inputDom.select();
          document.execCommand('Copy');
        },
        createInput:(()=>{
          var inputDom = null;
          return function(){
            if(inputDom){
              return inputDom;
            }
            inputDom = document.createElement('input')
            document.body.appendChild(inputDom);
            return inputDom;
          };
        })(),
      }
    }
</script>

<style scoped lang="scss">
  .modifyPage{
    width: 100%;
    height: 100%;
    padding: 20px  40px;
    .option-menu-card{
      display: flex;
      place-items: center;
      width: 100%;
      min-height: 60px;
      padding: 0 20px;
      margin-bottom: 20px;
      border-radius: 5px;
      background: #fff;
      box-shadow: rgba(0, 0, 0, 0.1) 0px 2px 12px 0px;
      box-sizing: border-box;
      .el-form{
        display: flex;
        place-content: space-between;
        width: 100%;
        padding: 10px 0;
      }
      .form-inline-warp{
        display: inline-block;
      }
      .el-form-item{
        margin-top: 5px;
        margin-bottom: 5px;
      }
      .snowfall-width{
        display: inline-flex;
        width: 0;
        overflow: hidden;
        white-space: nowrap;
        transition: all linear 0.4s;
      }
    }
    #modifyPage-mavon-editor{
      width: 100%;
      height: calc(100% - 80px);
    }
  }
</style>



